.featured-post {
  position: relative;
  width: fit-content;
  height: auto;
  margin: 0 auto;
}

.featured-post img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

.featured-post h2 {
  position: absolute;
  bottom: 0px;
  left: 0px;
  margin: 0;
  padding: 0;
}

.featured-post h2 span {
  display: inline-block;
  color: white;
  font: bold 24px/1.5 Helvetica, Sans-Serif;
  letter-spacing: -1px;
  background: rgba(0, 0, 0, 0.7);
  padding: 10px;
}

.featured-post span a {
  color: white;
  text-decoration: none;
}

@media (max-width: $tablet-width) {
  .featured-post h2 span {
    font: bold 17px/1.4 Helvetica, Sans-Serif;
  }
}

.pagination {
  text-align: center;
}
.pagination a {
  text-decoration: none;
}
.pagination-button {
  color: #999;
  border: 1px solid #e0e0e0;
  padding: 15px 45px;
  margin-top: 10px;
  margin-bottom: 10px;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
}
.pagination-active {
  color: black;
}
.pagination-active:hover {
  background-color: #e0e0e0;
}
