.post-content {
  width: 600px;
  display: block;
  margin: 10px auto 10px;
}
.post-date {
  display: block;
  margin-bottom: 1rem;
  color: #9a9a9a;
  font-size:0.8rem;
}
.featured-image img {
  align: middle;
  margin: 5px auto 25px;
  display: block;
  max-width: 600px;
}
@media (max-width: $container-width) {
  .post-content {
    width: 60vw;
    display: block;
    margin: 10px auto 10px;
  }
  .featured-image img {
    align: middle;
    margin: 5px auto 25px;
    display: block;
    max-width: 60vw;
  }
  p a img {
    align: middle;
    margin: 5px auto 25px;
    display: block;
    max-width: 60vw;
  }
  p img {
    align: middle;
    margin: 5px auto 25px;
    display: block;
    max-width: 60vw;
  }
  .related-thumbnail img {
    align: middle;
    max-width: 60vw;
  }
}
@media (max-width: $tablet-width) {
  .post-content {
    width: 95vw;
    display: block;
    margin: 10px auto 10px;
  }
  .featured-image img {
    align: middle;
    margin: 5px auto 25px;
    display: block;
    max-width: 95vw;
  }
  p a img {
    align: middle;
    margin: 5px auto 25px;
    display: block;
    max-width: 95vw;
  }
  p img {
    align: middle;
    margin: 5px auto 25px;
    display: block;
    max-width: 95vw;
  }
  .related-thumbnail img {
    align: middle;
    max-width: 95vw;
  }
}
.post-share {
  padding-bottom: 1rem;
}
.sharing-icons a {
  padding-right: 10px;
}

.related ul, ol, dl {
  margin: 0px;
}
.related h2 {
  padding-top: 15px;
  font-weight: bold;
  font-size: 1.3rem;
}
.related-posts {
  list-style: none;
}
.related-thumbnail {
  position: relative;
  height: auto;
  display: block;
}
.related img {
  align: middle;
  margin: 0px auto 5px;
  display: block;
}
